.App{
    background:
        -webkit-radial-gradient(white 1%, transparent 16%),
        -webkit-radial-gradient(white 1%, transparent  16%),
        #d2e1ff;
        background-position: 0 0, 80px 80px;
        background-size:160px 160px;
}

h1{
    font-family: 'Allura', regular;
    font-size: 64px;
    color: #c80032;
    margin-left: 450px;
    padding: 0 0 40px 0;
}
*{
    font-family: 'Open Sans', regular;
    margin: 0;
    padding: 0; 
}

@media screen and (max-width: 992px) {
    h1{
        white-space: nowrap;
        margin-left: 25%;
        }
    .App{
        background:
            -webkit-radial-gradient(white 1%, transparent 16%),
            -webkit-radial-gradient(white 1%, transparent  16%),
            #d2e1ff;
            background-position: 0 0, 50px 50px;
            background-size:100px 100px;
        }
}

/* On screens that are 600px or less, set the background color to olive */
@media screen and (max-width: 600px) {
    h1{
        margin: auto;
        }
}
.textbox{
    width: 550px;
    background-color: white;
    text-align: justify;
    margin: auto;
    padding: 20px;
    font-family: 'Open Sans', regular ;
    border-radius: 25px;
}

.medlems-knap{
    margin: 0px auto;
    width: 400px;
}

.knap{
    cursor: pointer;
    width: 400px;
    height: 100px;
    border-radius: 25px;
    font-size: 16pt;
    background-color: #001e6e;
    font-weight: bold;
    color: white;
}

.knap:hover{
    color: #c80032;
}

@media screen and (max-width: 992px) {
