ul{
    display: flex;
    flex-direction: row;
    list-style: none;
    justify-content: space-between;
    height: 100px;
    width: 100%;
}

li{
    font-size: 14pt;
    color: white;
    cursor: pointer;
    padding: 25px;
}

li:hover{
    color: grey;
}

img{
    margin-top: 15px;
    padding: 0 25px;
}

.dropdown-content{
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .dropdown .dropbutton {
    font-size: 16px;  
    border: none;
    outline: none;
    color: white;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
  }

  .dropdown-content a:hover {
    background-color: #ddd;
  }
  
  .dropdown:hover .dropdown-content {
    display: block;
  }

  .active-page{
    color: grey;
  }

  @media screen and (max-width: 992px) {
   li{
     display: none;
   }
  }
  
